<template>
  <div id="app-demo-window">
    <div class="one-block-1">
      <span>
        1. 自动更新
      </span>
    </div>  
    <div class="one-block-2">
      <a-space>
        <a-button @click="checkForUpdater()">检查更新</a-button>
        <a-button @click="download()">下载并安装</a-button>
      </a-space>
    </div>
    <!-- <div class="one-block-1">
      <span>
        2. 新窗口中加载html内容
      </span>
    </div>  
    <div class="one-block-2">
      <a-space>
        <a-button @click="createWindow(1)">打开html页面</a-button>
      </a-space>
    </div> -->
  </div>
</template>
<script>

export default {
  data() {
    return {
      status: 0, // -1:异常，1：有可用更新，2：没有可用更新，3：下载中, 4：下载完成
    };
  },
  mounted () {
    this.init();
  },
  methods: {
    init () {
      const self = this;
      self.$ipc.on('app.updater', (event, result) => {
        result = JSON.parse(result);
        console.log('app updater:', result);
        let text = result.desc;
        self.status = result.status;
        self.$message.info(text);
      })
    },
    checkForUpdater () {
      const self = this;
      self.$ipcCallMain('example.checkForUpdater').then(r => {
        console.log(r);
      })
    },
    download () {
      if (this.status !== 1) {
        this.$message.info('没有可用版本');
        return
      }
      const self = this;
      self.$ipcCallMain('example.downloadApp').then(r => {
        console.log(r);
      })
    },
  }
};
</script>
<style lang="less" scoped>
#app-demo-window {
  padding: 0px 10px;
  text-align: left;
  width: 100%;
  .one-block-1 {
    font-size: 16px;
    padding-top: 10px;
  }
  .one-block-2 {
    padding-top: 10px;
  }
}
</style>
